import React from 'react'
import classNames from 'classnames'
import { SubmenuItem } from '.'
import Hammer from 'hammerjs'
import '../App.css'

class MenuDrawer extends React.Component {
  componentDidMount() {
    // console.log(this.refs.menuDrawer)
    this.node = this.refs.menuDrawer
    this.hammer = new Hammer(this.node)
    this.hammer.get('pan').set({ 
      threshold: 0,
      direction: Hammer.DIRECTION_HORIZONTAL
    })
    this.hammer.on('panstart', e => {
      this.node.style.transition = ''
    })
    this.hammer.on('panmove', e => {
      if (e.deltaX > 0) this.node.style.transform = `translateX(${e.deltaX}px)`
    })
    this.hammer.on('panend', e => {
      console.log('panend', e.deltaX)
      this.node.style.transition = 'transform .3s'
      if (e.deltaX < 100) {
        this.node.style.transform = 'translateX(0)'
        this.props.setAppState({ menuIsOpen: true })
      } 
      else {
        this.node.style.transform = 'translateX(75vw)'
        this.props.setAppState({ menuIsOpen: false })
      }
    })
  }

  render() {
    const { menuIsOpen, sortBy, filter, setAppState } = this.props

    return (
      <div ref="menuDrawer" className={classNames({
        'Menu-drawer': true,
        'hidden': !menuIsOpen
      })}>
        <div className="Menu-close-button" onClick={() => {
          this.refs.menuDrawer.style.transform = 'translateX(75vw)'
          setAppState({ menuIsOpen: false })
        }}>×</div>
        <div className="Submenu">
          <div className="Submenu-title Sort-by">Sort</div>
          <div className="Submenu-item-container">
            <SubmenuItem sortBy={sortBy} setAppState={setAppState} label='Title'/>
            <SubmenuItem sortBy={sortBy} setAppState={setAppState} label='Composer'/>
            <SubmenuItem sortBy={sortBy} setAppState={setAppState} label='Year'/>
          </div>
        </div>
        <div className="Submenu">
          <div className="Submenu-title Filter">Filter</div>
          <div className="Submenu-item-container">
            <SubmenuItem filter={filter} setAppState={setAppState} label='All'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Jazz-tunes'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Standards'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Blues'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Rhythm'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Latin'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Waltz'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Holiday'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Miles'/>
            <SubmenuItem filter={filter} setAppState={setAppState} label='Sinatra'/>
          </div>
        </div>
      </div>
    )
  }
}

export default MenuDrawer


