import React from 'react'
import classNames from 'classnames'
import '../App.css'

const RandomScreen = ({ randomTune, getNewRandomTune, toggleSelected, setAppState }) => {
  const onClick = () => {
    toggleSelected(randomTune)
    setTimeout(() => {
      setAppState({ randomTune: getNewRandomTune() })
    }, 300)
  }

  return (
    <div className="Random-screen">
      <div 
        className={classNames({
          "Random-tune": true,
          "selected": randomTune.selected
        })}
        onClick={onClick}
      >
        <span className="Random-tune-title">
          {randomTune.title}
        </span>
        <span className="Random-tune-detail">
          {randomTune.composer} ({randomTune.year})
        </span>
      </div>
      <div className="Next-button" onClick={onClick}>
        NEXT
      </div>
    </div>
  )
}

export default RandomScreen


