import React from 'react'
import classNames from 'classnames'
import '../App.css'

const SubmenuItem = ({ label, sortBy, filter, setAppState }) => {
  const labelLowerCase = label.toLowerCase()

  const onClick = () => {
    if (sortBy) setAppState({ sortBy: labelLowerCase })
    else setAppState({ filter: labelLowerCase })
  }
  
  const getClassNames = () => {
    const current = sortBy ? sortBy : filter
    return classNames({
      'Submenu-item': true,
      'active': current === labelLowerCase
    })
  }
  
  const getLabel = () => {
    switch(labelLowerCase) {
      case 'jazz-tunes': return 'Jazz compositions'
      case 'rhythm':     return 'Rhythm changes'
      case 'holiday':    return 'Holiday songs'
      case 'waltz':      return 'Waltzes'
      case 'miles':      return 'Miles Davis'
      default:           return label
    }
  }

  return (
    <div className={getClassNames()} onClick={() => onClick()}>
      {getLabel()}
    </div>
  )
}

export default SubmenuItem


